<script>
import HttpService from '@/services/HttpService';

export default {
  data: () => ({
    roundsHeaders: [
      { text: 'epoch', value: 'epoch' },

      { text: 'UpDown', value: 'up_down' },
      { text: 'order', value: 'order_index' },
      { text: 'amt', value: 'amt' },
      //   { text: "income", value: "income" },
      //   { text: "payout", value: "payout" },

      { text: 'up', value: 'position_index_up' },
      { text: 'up1', value: 'position_index_test' },
      { text: 'up2', value: 'position_index_up2' },
      { text: 'down', value: 'position_index_down' },
      { text: 'down1', value: 'position_index_down1' },
      { text: 'down2', value: 'position_index_down2' },

      { text: 'time', value: 'lock_timestamp' },
    ],

    indexHeaders: [
      { text: '', value: 's', divider: true },
      { text: '1', value: 'i1', divider: true },
      { text: '2', value: 'i2', divider: true },
      { text: '3', value: 'i3', divider: true },
      { text: '4', value: 'i4', divider: true },
      { text: '5', value: 'i5', divider: true },
      { text: '6', value: 'i6', divider: true },
      { text: '7', value: 'i7', divider: true },
      { text: '8', value: 'i8', divider: true },
    ],

    rounds: [],
    amts_test1: [],
    amts_test2: [],
    amts_actual: [],
    amtList: [],

    summary: {
      followWinCount: 0,
      sumTestIncomeWin: 0,
      sumTestIncomeLose: 0,
      sumTest2IncomeWin: 0,
      sumTest2IncomeLose: 0,
      winCount: [],
    },

    settings: {},
  }),

  created: function () {
    this.setAmts();
    this.getSettings();
    this.init();
  },

  methods: {
    async init() {
      //   console.log(this.amts_test1);
      //   console.log(this.amts_test2);

      let rounds = await HttpService.getPlain('https://api.delog.io/api/v1/prediction/rounds');

      this.summary.followWinCount = 0;
      this.summary.positionWinCount = 0;
      this.summary.positionCount = 0;
      this.summary.position1WinCount = 0;
      this.summary.upCount = 0;
      this.summary.downCount = 0;

      this.summary.sumPayout = 0;
      this.summary.payoutCount = 0;

      this.summary.winCount = [
        { s: 'order', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
        { s: 'order', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
        { s: 'up' },
        { s: '' },
        { s: 'up1' },
        { s: '' },
        { s: 'up2' },
        { s: '' },
        { s: 'dn' },
        { s: '' },
        { s: 'dn1' },
        { s: '' },
        { s: 'dn2' },
        { s: '' },
      ];

      for (let item of rounds) {
        if (item.hash) {
          item.hashShort = item.hash.substr(0, 5);
        }

        if (item.up_down === 'UP') {
          this.summary.upCount++;
        } else {
          this.summary.downCount++;
        }

        if (item.order_position === 'UP' || item.order_position === 'DOWN') {
          this.summary.positionCount++;
          if (item.order_position === item.up_down) {
            let objKey = 'i' + item.order_index;
            this.summary.winCount[0][objKey]++;
            this.summary.positionWinCount++;

            if (item.order_index === 1) {
              this.summary.position1WinCount++;
            }
          } else {
            let objKey = 'i' + item.order_index;
            this.summary.winCount[1][objKey]++;
          }
        }

        let date = new Date(item.lock_timestamp).setHours(
          new Date(item.lock_timestamp).getHours() + 9
        );
        item.lock_timestamp = this.date2str(new Date(date), 'dd hh:mm');

        item.price = item.close_price - item.lock_price;
      }

      rounds = this.testPositionUp(rounds);
      rounds = this.testPositionDown(rounds);
      rounds = this.testPositionUp1(rounds);
      rounds = this.testPositionTest2(rounds);
      rounds = this.testPositionDownTest(rounds);
      rounds = this.testPositionDownTest2(rounds);

      this.testAmtProfit(rounds);
      this.rounds = rounds;
    },

    testAmtProfit(rounds) {
      let sumIncomeWin = 0;
      let sumIncomeLose = 0;
      let sumTestIncomeWin = 0;
      let sumTestIncomeLose = 0;
      let sumTest2IncomeWin = 0;
      let sumTest2IncomeLose = 0;

      for (let item of rounds) {
        // if (item.epoch > 58490) {
        let fee = 0.00047;

        let testAmt = Number(this.amts_test1[item.position_index - 1]);
        let testAmt2 = Number(this.amts_test2[item.position_index - 1]);
        if (isNaN(testAmt)) {
          testAmt = 0;
        }

        if (isNaN(testAmt2)) {
          testAmt2 = 0;
        }

        if (item.up_down === item.order_position && item.amt > 0) {
          let payout = 0;

          if (item.up_down === 'UP') {
            item.payout = item.bear_amt / item.bull_amt + 1;
            payout = item.bear_amt / item.bull_amt + 1;
          } else {
            item.payout = item.bull_amt / item.bear_amt + 1;
            payout = item.bull_amt / item.bear_amt + 1;
          }

          item.income = item.amt * payout * 0.97 - item.amt;
          let testIncome1 = testAmt * payout * 0.97 - testAmt;
          let testIncome2 = testAmt2 * payout * 0.97 - testAmt2;

          if (item.income > 0) item.income += fee;
          if (testIncome1 > 0) testIncome1 += fee;
          if (testIncome2 > 0) testIncome2 += fee;

          sumIncomeWin += item.income;
          sumTestIncomeWin += testIncome1;
          sumTest2IncomeWin += testIncome2;

          // console.log(item.epoch,' :',testIncome2)

          if (item.amt > 0) {
            this.summary.sumPayout += payout * 0.97;
            this.summary.payoutCount++;
          }
        } else if (item.amt > 0) {
          item.income = -item.amt;
          // console.log(item.income)
          let testIncome1 = -testAmt;
          let testIncome2 = -testAmt2;

          if (item.income > 0) item.income += fee;
          if (testIncome1 > 0) testIncome1 += fee;
          if (testIncome2 > 0) testIncome2 += fee;

          // console.log(item.epoch,' :',testIncome2)

          sumIncomeLose += item.income;
          sumTestIncomeLose += testIncome1;
          sumTest2IncomeLose += testIncome2;
        }

        // }
      }

      this.summary.sumIncomeWin = sumIncomeWin;
      this.summary.sumTestIncomeWin = sumTestIncomeWin;
      this.summary.sumTest2IncomeWin = sumTest2IncomeWin;

      this.summary.sumIncomeLose = sumIncomeLose;
      this.summary.sumTestIncomeLose = sumTestIncomeLose;
      this.summary.sumTest2IncomeLose = sumTest2IncomeLose;

      //   console.log(
      //     this.summary.sumTestIncomeWin,
      //     this.summary.sumTestIncomeLose
      //   );
    },

    testPositionUp(rounds) {
      let position = 'UP';
      this.incPositionCount(rounds, position, 'position_up', 'position_index_up');

      this.updateSummaryCount(rounds, position, 'position_up', 'position_index_up');
      const income = this.updateIncome(rounds, 'position_up', 'position_index_up');
      this.summary.sumPositionUpWin = income.win;
      this.summary.sumPositionUpLose = income.lose;
      return rounds;
    },

    testPositionDown(rounds) {
      let position = 'DOWN';
      this.incPositionCount(rounds, position, 'position_down', 'position_index_down');

      this.updateSummaryCount(rounds, position, 'position_down', 'position_index_down');
      const income = this.updateIncome(rounds, 'position_down', 'position_index_down');

      this.summary.sumPositionDownWin = income.win;
      this.summary.sumPositionDownLose = income.lose;
      return rounds;
    },

    testPositionUp1(rounds) {
      for (let i = rounds.length - 1; i >= 0; i--) {
        if (rounds[i].position_index_up === 1) {
          rounds[i].position_test = 'UP';
        }
      }

      let interval = 2;

      for (let i = rounds.length - interval + 1; i >= 0; i--) {
        let frame = this.getFrame(rounds, i + interval, interval + 50);
        let findItem1 = [];
        let targetEpoch = rounds[i].epoch % interval;

        for (let item of frame) {
          if (
            item &&
            (item.position_test === 'UP' || item.position_test === 'DOWN') &&
            item.epoch % interval === targetEpoch
          ) {
            findItem1.push(item);
          }
        }

        findItem1 = findItem1[0];
        if (findItem1 !== undefined) {
          if (findItem1.position_test === 'UP' || findItem1.position_test === 'DOWN') {
            if (findItem1.up_down === findItem1.position_test) {
              rounds[i].position_index_test = 1;
            } else {
              rounds[i].position_index_test = findItem1.position_index_test + 1;
            }
          } else {
            rounds[i].position_index_test = findItem1.position_index_test;
          }
        }
      }

      this.updateSummaryCount(rounds, 'test', 'position_test', 'position_index_test');
      //   const income = this.updateIncome(
      //     rounds,
      //     "position_test",
      //     "position_index_test"
      //   );
      //   this.summary.sumPositionTestWin = income.win;
      //   this.summary.sumPositionTestLose = income.lose;
      return rounds;
    },

    testPositionTest2(rounds) {
      const targetPosition = 'position_up2';
      const targetPositionIndex = 'position_index_up2';

      for (let i = rounds.length - 1; i >= 0; i--) {
        let position = '';
        if (rounds[i].position_index_test === 1) {
          position = rounds[i].position_test;
        }
        rounds[i][targetPosition] = position;
      }

      let interval = 2;

      for (let i = rounds.length - interval + 1; i >= 0; i--) {
        let frame = this.getFrame(rounds, i + interval, interval + 90);
        let findItem1 = [];
        let targetEpoch = rounds[i].epoch % interval;

        for (let item of frame) {
          if (
            item &&
            (item[targetPosition] === 'UP' || item[targetPosition] === 'DOWN') &&
            item.epoch % interval === targetEpoch
          ) {
            findItem1.push(item);
          }
        }

        findItem1 = findItem1[0];
        if (findItem1 !== undefined) {
          if (findItem1[targetPosition] === 'UP' || findItem1[targetPosition] === 'DOWN') {
            if (findItem1.up_down === findItem1[targetPosition]) {
              rounds[i][targetPositionIndex] = 1;
            } else {
              rounds[i][targetPositionIndex] = findItem1[targetPositionIndex] + 1;
            }
          } else {
            rounds[i][targetPositionIndex] = findItem1[targetPositionIndex];
          }
        }
      }

      this.updateSummaryCount(rounds, 'test2', targetPosition, targetPositionIndex);
      const income = this.updateIncome(rounds, targetPosition, targetPositionIndex);

      this.summary.sumPositionTest2Win = income.win;
      this.summary.sumPositionTest2Lose = income.lose;
      return rounds;
    },

    testPositionDownTest(rounds) {
      const targetPosition = 'position_down1';
      const targetPositionIndex = 'position_index_down1';

      for (let i = rounds.length - 1; i >= 0; i--) {
