        if (rounds[i].position_index_down === 1) {
          position = rounds[i].position_down;
        }
        // if (rounds[i].position_index_down === 1) {
        //   position = rounds[i].position_down;
        // }

        rounds[i][targetPosition] = position;
      }

      let interval = 2;

      for (let i = rounds.length - interval + 1; i >= 0; i--) {
        let frame = this.getFrame(rounds, i + interval, interval + 50);
        let findItem1 = [];
        let targetEpoch = rounds[i].epoch % interval;

        for (let item of frame) {
          if (
            item &&
            (item[targetPosition] === 'UP' || item[targetPosition] === 'DOWN') &&
            item.epoch % interval === targetEpoch
          ) {
            findItem1.push(item);
          }
        }

        findItem1 = findItem1[0];
        if (findItem1 !== undefined) {
          if (findItem1[targetPosition] === 'UP' || findItem1[targetPosition] === 'DOWN') {
            if (findItem1.up_down === findItem1[targetPosition]) {
              rounds[i][targetPositionIndex] = 1;
            } else {
              rounds[i][targetPositionIndex] = findItem1[targetPositionIndex] + 1;
            }
          } else {
