    },

    incPositionCount(rounds, position, _position, _position_index) {
      for (let i = rounds.length - 3; i >= 0; i--) {
        rounds[i][_position] = position;
        if (rounds[i + 2].up_down === rounds[i + 2][_position]) {
          rounds[i][_position_index] = 1;
        } else {
          rounds[i][_position_index] = rounds[i + 2][_position_index] + 1;
        }
      }
    },

    updateSummaryCount(rounds, position, _position, _position_index) {
      let winWinCount = this.summary.winCount[2];
      let loseWinCount = this.summary.winCount[3];

      let staticPosition = position;

      if (position === 'test') {
        staticPosition = 'UP';
        winWinCount = this.summary.winCount[4];
        loseWinCount = this.summary.winCount[5];
      } else if (position === 'test2') {
        staticPosition = 'UP';
        winWinCount = this.summary.winCount[6];
        loseWinCount = this.summary.winCount[7];
      } else if (position === 'DOWN') {
        winWinCount = this.summary.winCount[8];
        loseWinCount = this.summary.winCount[9];
      } else if (position === 'test3') {
        staticPosition = 'DOWN';
        winWinCount = this.summary.winCount[10];
        loseWinCount = this.summary.winCount[11];
      } else if (position === 'test4') {
        staticPosition = 'DOWN';
        winWinCount = this.summary.winCount[12];
        loseWinCount = this.summary.winCount[13];
      }

      for (let i = 1; i < 9; i++) {
        winWinCount['i' + i] = { total: 0, short: 0 };
        loseWinCount['i' + i] = { total: 0, short: 0 };
      }
      for (let i = rounds.length - 3; i >= 0; i--) {
        if (rounds[i][_position] === 'UP' || rounds[i][_position] === 'DOWN') {
          if (rounds[i].up_down === rounds[i][_position]) {
            let objKey = 'i' + rounds[i][_position_index];
            if (winWinCount[objKey]) {
              winWinCount[objKey].total++;
            }
          } else {
            let objKey = 'i' + rounds[i][_position_index];
            if (loseWinCount[objKey]) {
              loseWinCount[objKey].total++;
            }
          }
        }
      }

      for (let i = 1; i < 9; i++) {
        let objKey = 'i' + i;
        let percent =
          (winWinCount[objKey].total / (winWinCount[objKey].total + loseWinCount[objKey].total)) *
          100;
        percent = percent.toFixed(2);

        winWinCount[objKey].totalValue = `${winWinCount[objKey].total} (${percent})`;
        winWinCount[objKey].totalPercent = Number(percent);
        winWinCount[objKey].position = staticPosition;

        loseWinCount[objKey].totalValue = loseWinCount[objKey].total;
      }

      let shortSize = 80;

      for (let i = 0; i < shortSize; i++) {
        if (rounds[i][_position] === 'UP' || rounds[i][_position] === 'DOWN') {
          if (rounds[i].up_down === rounds[i][_position]) {
            let objKey = 'i' + rounds[i][_position_index];
            if (winWinCount[objKey]) {
              winWinCount[objKey].short++;
            }
          } else {
            let objKey = 'i' + rounds[i][_position_index];
            if (loseWinCount[objKey]) {
              loseWinCount[objKey].short++;
            }
          }
        }
      }

      for (let i = 1; i < 9; i++) {
        let objKey = 'i' + i;
        let percent =
          (winWinCount[objKey].short / (winWinCount[objKey].short + loseWinCount[objKey].short)) *
          100;
        percent = percent.toFixed(2);

        winWinCount[objKey].shortValue = `${winWinCount[objKey].short} (${percent})`;
        winWinCount[objKey].shortPercent = Number(percent);
        winWinCount[objKey].position = staticPosition;

        loseWinCount[objKey].shortValue = loseWinCount[objKey].short;
      }
    },

    updateIncome(rounds, _position, _position_index) {
      let sumTestPositionShortWin = 0;
      let sumTestPositionShortLose = 0;
      for (let i = rounds.length - 3; i >= 0; i--) {
        let fee = 0.00047;

        if (rounds[i][_position] === 'UP' || rounds[i][_position] === 'DOWN') {
          let tempAmt = this.amts_test1[rounds[i][_position_index] - 1];

          if (rounds[i].up_down === rounds[i][_position]) {
            let payout = 0;
            if (rounds[i].up_down === 'UP') {
              payout = rounds[i].bear_amt / rounds[i].bull_amt + 1;
            } else {
              payout = rounds[i].bull_amt / rounds[i].bear_amt + 1;
            }

            let testIncome2 = tempAmt * payout * 0.97 - tempAmt;
            if (testIncome2 > 0) testIncome2 += fee;

            if (tempAmt !== undefined) {
              sumTestPositionShortWin += testIncome2;
              rounds[i].amt_test = testIncome2;
            }
          } else {
            let testIncome2 = -tempAmt;
            if (testIncome2 > 0) testIncome2 += fee;
            if (tempAmt !== undefined) {
              sumTestPositionShortLose += testIncome2;
              rounds[i].amt_test = testIncome2;
            }
          }
        }
      }
      return { win: sumTestPositionShortWin, lose: sumTestPositionShortLose };
    },

    getFrame(rounds, start, size) {
      let list = [];
      for (let i = start; i < start + size; i++) {
        list.push(rounds[i]);
      }
      return list;
    },

