    winColor(up_down, index, position, status) {
      let color = 'white lighten-3';

      if (up_down === position) {
        if (up_down === 'UP') {
          if (index >= 6) {
            color = 'green lighten-2';
          } else if (index >= 5) {
            color = 'green lighten-3';
          } else if (index >= 4) {
            color = 'green lighten-4';
          } else if (index >= 1) {
            color = 'green lighten-5';
          }
        } else {
          if (index >= 6) {
            color = 'red lighten-2';
          } else if (index >= 5) {
            color = 'red lighten-3';
          } else if (index >= 4) {
            color = 'red lighten-4';
          } else if (index >= 1) {
            color = 'red lighten-5';
          }
        }
      } else {
        // if (index == 1) {
        // color = 'blue lighten-4'
        // }
      }
      return color;
    },

    countColor(percent) {
      if (percent > 50 && percent < 90) {
        if (percent > 70) {
          return 'green lighten-3';
        } else if (percent > 60) {
          return 'green lighten-4';
        } else if (percent > 51) {
          return 'green lighten-5';
        }
      } else if (percent < 50) {
        if (percent < 30) {
          return 'red lighten-4';
        } else if (percent < 40) {
          return 'red lighten-5';
        }
      }

      return 'white lighten-3';
    },

    date2str(x, y) {
      var z = {
        M: x.getMonth() + 1,
        d: x.getDate(),
        h: x.getHours(),
        m: x.getMinutes(),
        s: x.getSeconds(),
      };
      y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
        return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2);
      });

      return y.replace(/(y+)/g, function (v) {
        return x.getFullYear().toString().slice(-v.length);
      });
    },

    cToFixed(num, size) {
      try {
        return num.toFixed(size);
      } catch (e) {
        return '';
      }
    },

    cSubstr(txt, index) {
      let tResult = '';
      try {
        tResult = txt.substring(0, index).toLowerCase();
      } catch (e) {
        console.log('error');
      }
      return tResult;
    },
